@Injectable({
	providedIn: 'root'
})
export class CustomerService {

	customer = null;

	constructor(private http: HttpClient, private socialAuthService: AuthService) {
		// TODO: customer가 깨졌을 경우를 처리해줘야 한다.
		this.customer = JSON.parse(localStorage.getItem('customer'));
	}

	socialSignIn(socialPlatform: string) {
		let socialPlatformProvider;
		if (socialPlatform === 'facebook') {
			socialPlatformProvider = FacebookLoginProvider.PROVIDER_ID;
		} else if (socialPlatform === 'google') {
			socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
		}

		return this.socialAuthService.signIn(socialPlatformProvider).then(
			(customer) => {
				console.log(socialPlatform + ' sign in data : ' , customer);
				this.customer = customer;
				localStorage.setItem('customer', JSON.stringify(customer));

				// if not registered, then create customer
				// this.customerService.createCustomer(userData.email);
			}
		);
	}

	isLoggedIn() {
		return (this.customer !== null);
	}

	signOut() {
		this.socialAuthService.signOut();
		this.customer = null;
		localStorage.removeItem('customer');
	}

	createCustomer(email: string) {
		const newCustomer = {
			email_address: email
		};


	}
}
