import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../services/order.service';
import { MatDialog } from '@angular/material';
import { DetailModalComponent } from '../detail-modal/detail-modal.component';

@Component({
	selector: 'app-products',
	templateUrl: './products.component.html',
	styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
	all_products: any[];
	products: any[];
	child_categories: any[];
	currentCategory: number;
	currentChildCategory: number;

	constructor(private orderService: OrderService, private dialog: MatDialog) { }

	ngOnInit() {
		this.orderService.getAllProducts().subscribe((data: any[]) => {
			this.all_products = data;
			if ((new Date()).getHours() > 14) { this.all_products.splice(1, 1); } // exclude 'Lunch' after 2 PM
			this.onClickCategory(0); // make 0th category as default
		}, (error) => { console.log(error); });
	}

	onClickCategory(i: number) {
		this.currentCategory = i;
		this.child_categories = this.all_products[i].child_categories;
		if (this.all_products[i].child_categories.length > 0) { // if the category has its child categories.
			this.onClickChildCategory(0);
		} else { this.products = this.all_products[i].products; }
	}

	onClickChildCategory(i: number) {
		this.currentChildCategory = i;
		this.products = this.all_products[this.currentCategory].child_categories[i].products;
	}

	openDetailModal(product: any) {
		this.dialog.open(DetailModalComponent, {
			width: '50%',
			data: JSON.parse(JSON.stringify(product)) // deep copy
		});
	}
}
