import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../services/order.service';

@Component({
	selector: 'app-tip',
	templateUrl: './tip.component.html',
	styleUrls: ['./tip.component.scss']
})
export class TipComponent implements OnInit {

	tipRates: number[] = [0.15, 0.2, 0.25, 0.3];
	currentTipType: string; // To indicate active button, 'rate' or 'custom'
	currentTipRate: number; // To indicate active button
	customTipPrice: number;
	order: any;

	constructor(private orderService: OrderService) {
		this.order = this.orderService.getOrder();
	}

	ngOnInit() {
	}

	setTipWithRate() {
		this.order.tipPrice = Number((this.currentTipRate * (this.order.subTotalPrice + this.order.taxPrice)).toFixed(2));
	}

	setTipWithCustom() {
		if (!this.customTipPrice || this.customTipPrice < 0) { this.customTipPrice = 0; }

		this.order.tipPrice = this.customTipPrice;
	}

}
